
import { defineComponent } from 'vue';
import store from '../store'
import gsap from 'gsap'
import ScrollToPlugin from "gsap/ScrollToPlugin";

gsap.registerPlugin(ScrollToPlugin)

export default defineComponent({
  name: 'Book',
  props: {
    id: Number,
    title: String,
    author: String,
    cover: String,
    year: String,
    description: String,
    index: Number,
    url: String,
    isbn: String,
    isSearch: Boolean
  },
  setup() {
    return {store}
  },

  data(){
     return {
       descriptionValue: this.description,
       authorPhoto:'',
       authorBio: '',
       info: {},
       backButton: require('../assets/backButton.png'),
       bookInfoTimeline: gsap.timeline(),
       bookTimeline: gsap.timeline(),
       isMobile: window.innerWidth < window.innerHeight
     }
  },
  methods: {
     
      showDescription() {
        const book = document.querySelector('.book') as HTMLElement
        if (!book) return
        console.log(book.offsetWidth, book.scrollWidth)
        // gsap.to(book, 1, {scrollTo: {x: 'max'}})
      }
  }
})
